import { request } from 'utils';

const prefix = '/api/organization';

export const getDocIns = ({
  id,
  docInNo,
  docInDate,
  sentPlace,
  noteDate,
  docContent,
  savedPlace,
  recipientUnit,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/DocIn',
    method: 'GET',
    params: {
      id,
      docInNo,
      docInDate,
      sentPlace,
      noteDate,
      docContent,
      savedPlace,
      recipientUnit,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });



